(() => {
  let isMenuOpen = false;
  function toggleMenu() {
    const expanded =
      menuBtnRef.getAttribute('aria-expanded') === 'true' || false;
    menuBtnRef.classList.toggle('is-open');
    menuBtnRef.setAttribute('aria-expanded', !expanded);
    mobileMenuRef.classList.toggle('is-open');
    headerLogoRef.classList.toggle('white');

    isMenuOpen = !isMenuOpen;
  }

  const menuBtnRef = document.querySelector('[data-menu-button]');
  const mobileMenuRef = document.querySelector('[data-menu]');
  const headerLogoRef = document.querySelector('[data-header-logo]');

  menuBtnRef.addEventListener('click', () => {
    toggleMenu();
  });

  var linkNav = document.querySelectorAll('[href^="#"]'), //–≤—ã–±–∏—Ä–∞–µ–º –≤—Å–µ —Å—Å—ã–ª–∫–∏ –∫ —è–∫–æ—Ä—é –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
    V = 0.3; // —Å–∫–æ—Ä–æ—Å—Ç—å, –º–æ–∂–µ—Ç –∏–º–µ—Ç—å –¥—Ä–æ–±–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ —á–µ—Ä–µ–∑ —Ç–æ—á–∫—É (—á–µ–º –º–µ–Ω—å—à–µ –∑–Ω–∞—á–µ–Ω–∏–µ - —Ç–µ–º –±–æ–ª—å—à–µ —Å–∫–æ—Ä–æ—Å—Ç—å)
  for (var i = 0; i < linkNav.length; i++) {
    linkNav[i].addEventListener(
      'click',
      function (e) {
        if (isMenuOpen) {
          toggleMenu();
        }
        //–ø–æ –∫–ª–∏–∫—É –Ω–∞ —Å—Å—ã–ª–∫—É
        e.preventDefault(); //–æ—Ç–º–µ–Ω—è–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–µ –ø–æ–≤–µ–¥–µ–Ω–∏–µ
        var w = window.pageYOffset, // –ø—Ä–æ–∏–∑–≤–æ–¥–∏–º –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∞
          hash = this.href.replace(/[^#]*(.*)/, '$1'); // –∫ id —ç–ª–µ–º–µ–Ω—Ç–∞, –∫ –∫–æ—Ç–æ—Ä–æ–º—É –Ω—É–∂–Ω–æ –ø–µ—Ä–µ–π—Ç–∏
        (t = document.querySelector(hash).getBoundingClientRect().top), // –æ—Ç—Å—Ç—É–ø –æ—Ç –æ–∫–Ω–∞ –±—Ä–∞—É–∑–µ—Ä–∞ –¥–æ id
          (start = null);
        requestAnimationFrame(step); // –ø–æ–¥—Ä–æ–±–Ω–µ–µ –ø—Ä–æ —Ñ—É–Ω–∫—Ü–∏—é –∞–Ω–∏–º–∞—Ü–∏–∏ [developer.mozilla.org]
        function step(time) {
          if (start === null) start = time;
          var progress = time - start,
            r =
              t < 0
                ? Math.max(w - progress / V, w + t)
                : Math.min(w + progress / V, w + t);
          window.scrollTo(0, r);
          if (r != w + t) {
            requestAnimationFrame(step);
          } else {
            location.hash = hash; // URL —Å —Ö—ç—à–µ–º
          }
        }
      },
      false,
    );
  }
})();
